@tailwind base;
@tailwind components;
@tailwind utilities;


@layer base {
 
 *{
    margin: 0 auto;
    padding: 0 auto;
    box-sizing: border-box;
    color: white;
 }
    header {
        background-color: #606060;
    }
    li {
        style: none;
    }
    a {
        color: white;
    }

    i {
        color: yellow;
        max-width: 5rem;
    }
}

@layer components {

    .container {
        width: 100%;
        max-width: 375px;

    }

    .navBar {
        min-height: 70px;
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding: 0 24px;
    
    }
    .nav-menu {
        display: flex;
        justify-content: space-between;
        align-items: center;
        gap: 60px;

    }
    .nav-branding {
        font-size:1.5rem;
    }
    .val-link{
        transition: 0.7s ease;
    }

    .nav-link:hover {
        color: dodgerblue;
    }

    .hamburger {
        display: none;
        cursor: pointer;
    }
    .bar {
        display: block;
        width: 25px;
        height: 3px;
        margin: 5px auto;
        -webkit-transition: all 0.3s ease-in-out;
        background-color: white;
        
    }
    @media(max-width:2000px) {
        .hamburger {
            display: block
        }
        .hamburger .active .bar:nth-child(2){
            opacity: 0
        }

        .hamburger .active .bar:nth-child(1){
            transform: translateY(8px) rotate(45deg);
        }
        .hamburger .active .bar:nth-child(3){
            transform: translateY(-8px) rotate(-45deg);
        }


        
        .nav-menu{
            position: fixed;
            left: -100%;
            top: 70px;
            flex-direction: column;
            background-color: #606060;
            width: 100%;
            text-align: center;
            transition: 0.3px;
        }
        .nav-item{
            margin: 16px 0;
        }

        .nav-menu.active{
            left: 0
        }
    }

 

    .carousel-items {
        display: flex;
        overflow-x: scroll;
        padding: 1rem 0;
        scroll-snap-type: x mandatory;
    }
    
    .carousel-item {
        flex: 1 0 250px;
        margin-left: 1rem;
        scroll-snap-align: start;
        padding-left: 1rem;
    }

    .mainCard {
        width: 350px;
        background-color: #606060;
        
    }

    .card {
        width: 250px;
        background-color: #606060;
        
    }

    .titleRubriques {
        padding-left: 1rem;
        margin-top: 1rem;
        margin-bottom: 1rem;
        font-weight: bold;
    }
    
}